import InstrumentView from 'd:/JS/2019Q1/stage2/enerjaizzzer-2019Q1/piskel-clone/src/views/instrumentView';

export default class ModelPen {
  constructor(size, color, targetX, targetY, arr) {
    this.canvas = document.querySelector('.canvas__general--working-canvas');
    this.size = size;
    this.color = color;
    this.targetX = targetX;
    this.targetY = targetY;
    this.arr = arr;
  }

  modelPenStart() {
    this.target();
    this.canvasArr();
  }

  target() {
    this.CanvasX = Math.floor(this.targetX / this.size.onePiksel);
    if (this.CanvasX >= this.size.size) { this.CanvasX = this.size.size - 1; }
    this.CanvasY = Math.floor(this.targetY / this.size.onePiksel);
    if (this.CanvasY >= this.size.size) { this.CanvasY = this.size.size - 1; }
    this.oldCanvasX = Math.floor(this.arr[0] / this.size.onePiksel);
    if (this.oldCanvasX >= this.size.size) { this.oldCanvasX = this.size.size - 1; }
    this.oldCanvasY = Math.floor(this.arr[1] / this.size.onePiksel);
    if (this.oldCanvasY >= this.size.size) { this.oldCanvasY = this.size.size - 1; }
  }

  canvasArr() {
    this.arrColor = this.size.defaultColor;
    this.newColor = this.color.newColor;

    this.arrColor[this.CanvasY][this.CanvasX] = this.newColor;

    if (this.oldCanvasX - this.CanvasX !== 0 || this.oldCanvasY - this.CanvasY !== 0) {
      this.line(this.oldCanvasX, this.oldCanvasY, this.CanvasX, this.CanvasY);
    }
    const renderCanvas = new InstrumentView(this.arrColor, this.size);
    renderCanvas.viewPenStart();
  }

  line(oldCanvasX1, oldCanvasY1, CanvasX, CanvasY) {
    let oldCanvasX = oldCanvasX1;
    let oldCanvasY = oldCanvasY1;
    const deltaX = Math.abs(CanvasX - oldCanvasX);
    const deltaY = Math.abs(CanvasY - oldCanvasY);
    const signX = (oldCanvasX < CanvasX) ? 1 : -1;
    const signY = (oldCanvasY < CanvasY) ? 1 : -1;
    let err = deltaX - deltaY;
    const tr = true;

    while (tr) {
      this.arrColor[oldCanvasY][oldCanvasX] = this.newColor;

      if ((oldCanvasX === CanvasX) && (oldCanvasY === CanvasY)) break;
      const error = 2 * err;
      if (error > -deltaY) {
        err -= deltaY;
        oldCanvasX += signX;
      }
      if (error < deltaX) {
        err += deltaX;
        oldCanvasY += signY;
      }
    }
  }
}
