export default class DefaultColorView {
  constructor(canvas, colors) {
    this.canvas = canvas;
    this.colors = colors;
  }

  render() {
    const renderCanvas = this.canvas;
    const ctx = renderCanvas.getContext('2d');
    const renderOldColor = this.colors.oldColor;
    const renderNewColor = this.colors.newColor;

    ctx.fillStyle = renderOldColor;
    ctx.fillRect(72, 60, 80, 80);

    ctx.fillStyle = renderNewColor;
    ctx.fillRect(32, 20, 80, 80);
  }
}
