import SizeModel from '../models/sizeModel';
import SizeView from '../views/sizeView';

export default class Size {
  constructor(root) {
    this.root = root;
    this.buttonSize32 = document.querySelector('.general__canvas-size--32x32');
    this.buttonSize64 = document.querySelector('.general__canvas-size--64x64');
    this.buttonSize128 = document.querySelector('.general__canvas-size--128x128');
  }

  start() {
    this.addListeners();
  }

  next(size) {
    this.model = new SizeModel(size);
    this.data = this.model.getModel();
    this.view = new SizeView(this.data);
    this.view.render();
  }

  addListeners() {
    this.buttonSize32.addEventListener('click', () => {
      const size = 32;
      this.next(size);
    });
    this.buttonSize64.addEventListener('click', () => {
      const size = 64;
      this.next(size);
    });
    this.buttonSize128.addEventListener('click', () => {
      const size = 128;
      this.next(size);
    });
  }
}
