export default class SizeModel {
  constructor(size) {
    this.size = size;
    this.windowSize = 768;
    this.data = {};
  }

  getModel() {
    this.getColor();
    this.getSize();
    return this.data;
  }

  getColor() {
    const defaultColorArr = [...Array(this.size)].map(() => Array(this.size));

    for (let i = 0; i < this.size; i += 1) {
      for (let j = 0; j < this.size; j += 1) {
        if ((i + j) % 2 === 0) {
          defaultColorArr[i][j] = '#d4f5f4';
        } else if ((i + j) % 2 === 1) {
          defaultColorArr[i][j] = '#bedddc';
        }
      }
    }

    this.data.defaultColor = defaultColorArr.map(arr => arr.slice());
  }

  getSize() {
    this.data.onePiksel = this.windowSize / this.size;
    this.data.size = this.size;
  }
}
