import ModelPen from 'd:/JS/2019Q1/stage2/enerjaizzzer-2019Q1/piskel-clone/src/models/instruments/pen';

function addListeners(size, color) {
  let targetX;
  let targetY;
  const arr = [];
  let mouseDown = false;
  const canvas = document.querySelector('.canvas__general--working-canvas');

  const down = () => {
    mouseDown = true;
  };

  const move = (event) => {
    if (mouseDown) {
      targetX = event.offsetX;
      targetY = event.offsetY;
      arr.push(targetX, targetY);
      if (arr.length > 4) {
        arr.shift();
        arr.shift();
      }
      const modelPen = new ModelPen(size, color, targetX, targetY, arr);
      modelPen.modelPenStart();
    }
  };

  const up = () => {
    mouseDown = false;
    canvas.removeEventListener('mousedown', down);
    canvas.removeEventListener('mousemove', move);
    document.removeEventListener('mouseup', up);
  };

  canvas.addEventListener('mousedown', down);

  canvas.addEventListener('mousemove', move);

  document.addEventListener('mouseup', up);
}

export default addListeners;
