import addListener from './instruments/pen';

import ColorSelect from '../models/instruments/colorSelect';

import PaintBucket from '../models/instruments/paintBucket';

import Ereser from '../models/instruments/ereser';

import Stroke from '../models/instruments/stroke';

import Circle from '../models/instruments/circle';

import Move from '../models/instruments/move';

import ShapeSelection from '../models/instruments/shapeSelection';

import Lighten from '../models/instruments/lighten';

import Dithering from '../models/instruments/dithering';

export default class Instruments {
  constructor(instrument, size, color) {
    this.targetInstrument = instrument;
    this.size = size;
    this.color = color;
  }

  start() {
    if (this.targetInstrument.classList[0] === 'instruments__buttons--pen') {
      addListener(this.size, this.color);
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--color-select') {
      const instrument = new ColorSelect();
      instrument.start();
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--paint-bucket') {
      const instrument = new PaintBucket();
      instrument.start();
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--ereser') {
      const instrument = new Ereser();
      instrument.start();
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--stroke') {
      const instrument = new Stroke();
      instrument.start();
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--circle') {
      const instrument = new Circle();
      instrument.start();
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--move') {
      const instrument = new Move();
      instrument.start();
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--shape-selection') {
      const instrument = new ShapeSelection();
      instrument.start();
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--lighten') {
      const instrument = new Lighten();
      instrument.start();
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--dithering') {
      const instrument = new Dithering();
      instrument.start();
    } else { console.log('no instrument'); }
  }
}
