export default class SizeView {
  constructor(data) {
    this.data = data;
    this.buttons = document.querySelector('.general__canvas-size');
    this.canvas = document.querySelector('.canvas__general--working-canvas');
    this.page = document.querySelector('.pages__page-operations--page');
    this.animation = document.querySelector('.container__animation--canvas');
  }

  render() {
    this.delButtons();
    this.renderGeneralCanvas();
    this.renderPageCanvas();
    this.renderAnimationCanvas();
  }

  view(ctx, fullSize, piksel) {
    for (let i = 0; i < fullSize; i += 1) {
      for (let j = 0; j < fullSize; j += 1) {
        ctx.fillStyle = this.data.defaultColor[i][j];
        ctx.fillRect(piksel * j, piksel * i, piksel * (j + 1), piksel * (i + 1));
      }
    }
  }

  delButtons() {
    this.canvas.classList.remove('none');
    this.buttons.classList.add('none');
    this.page.classList.remove('none');
    this.animation.classList.remove('none');
  }

  renderGeneralCanvas() {
    const generalCanvas = this.canvas;
    const ctx = generalCanvas.getContext('2d');
    const fullSize = this.data.size;
    const piksel = this.data.onePiksel;
    this.view(ctx, fullSize, piksel);
  }

  renderPageCanvas() {
    const canvas = this.page;
    const ctx = canvas.getContext('2d');
    const fullSize = this.data.size;
    const piksel = this.data.onePiksel / 6;
    this.view(ctx, fullSize, piksel);
  }

  renderAnimationCanvas() {
    const canvas = this.animation;
    const ctx = canvas.getContext('2d');
    const fullSize = this.data.size;
    const piksel = this.data.onePiksel / 3;
    this.view(ctx, fullSize, piksel);
  }
}
