import DefaultColorModel from '../models/defaultColorModel';
import DefaultColorView from '../views/defaultColorView';

export default class DefaultColor {
  constructor() {
    this.canvasColors = document.querySelector('.instruments__colors--color');
  }

  start() {
    this.model = new DefaultColorModel(this.canvasColors);
    this.data = this.model.getModel();
    this.view = new DefaultColorView(this.canvasColors, this.data);

    this.view.render();
  }
}
